@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700);
body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.country-components-container {
  display: flex;
  flex-direction: row;
}

nav {
  display: flex;
  flex-direction: row;
  padding: 1em;
  padding-bottom: 1em;
  border-bottom: solid 1px rgb(182, 182, 182);
}

.dropdown {
  margin-left: auto;
}

.dropbtn {
  background-color: white;
  border: solid 1px rgb(182, 182, 182);
  color: rgb(128, 128, 128);
  padding: 10px 25px;
  /* border-radius: 5px; */
  cursor: pointer;
}

.dropbtn:hover {
  background-color: rgb(128, 128, 128);
  border: solid 1px rgb(182, 182, 182);
  color: white;
}

.country-search-input {
  padding-left: 1em;
}

.country-list-container {
  display: flex;
  flex-direction: column;  
  border-right: solid 1px rgb(182, 182, 182);
}

ul {
  list-style: none;
  margin: 0;
}

li {
  border-bottom: solid 1px rgb(182, 182, 182);
  padding: 1em 2em;
}

.sidebar-countryname {
  cursor: pointer;
  /* color: rgb(182, 182, 182); */
  color: rgb(54, 54, 54);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.sidebar-countryname:hover {
  color: rosybrown;
  /* text-decoration: underline solid black; */
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.country-display-container {
  padding-top: 1em;
  padding-left: 5em;
  display: flex;
  flex-direction: row;
}

